import React from 'react';
import './card.css'
import Skeleton from '@material-ui/lab/Skeleton';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Badge from '@material-ui/core/Badge';
const useStyles = makeStyles(theme => ({
    name: {
        overflow: "hidden",
        textOverflow: "ellipsis",
        whiteSpace: "nowrap",
        marginTop: theme.spacing(1)
    },
}));


export default props => {
    const classes = useStyles();
    return(
        <>
        <div className="card_head">
            <div className="img_spacer" ></div>
            {!props.load
                ?<img className='card_head_img' src={props.src}></img>
                :<img className={'card_head_img MuiSkeleton-pulse'} style={{border:'none', background:'rgba(0, 0, 0, 0.04)'}}></img>
            }
 
        </div>
    
        <div className="card_body">
            <Typography color="textPrimary" className={classes.name} variant="subtitle2">
            {!props.load
                ?props.title
                :<Skeleton className=""  width="100%" />
            }
            </Typography>
            <Typography color="primary" variant="caption">
            {!props.load
                ?props.subtitle
                :<Skeleton className="" width="80%" />
            }
            </Typography>
        </div>
        </>
    )
}