import React, {useEffect,useState} from 'react';
import Liked from './SubPage/liked'
import NavBar from '../../Components/navbar'
import Container from '@material-ui/core/Container';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import { ThemeProvider } from '@material-ui/core/styles';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import IconButton from '@material-ui/core/IconButton';
import { observer, inject} from 'mobx-react';
import axios from "axios";
import {Routes} from "../../Routes"
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
   useParams
} from "react-router-dom";

const useStyles = makeStyles(theme => ({
  cover: {
    width: '100%',
    height: 460,
    position: 'relative',
    'background-size': 'cover',
    'background-repeat': 'no-repeat',
    'background-position': 'center',
    // 'background-image': props => props.url,
    '&::before': {
      top: 0,
      left: 0,
      width: '100%',
      height: '100%',
      content: " ",
      position: 'absolute',
      'background-image': 'linear-gradient(-180deg, rgba(0,0,0,0.00) 58%, rgba(0,0,0,0.32) 100%)',
    }
  },
}));


const UserCover = observer(({userStore}) => {
  let classes = useStyles();
  return (
    <div className={classes.cover} style={{backgroundImage:`url(${userStore.user.coverUrl})`}}></div>
  )
})


export default inject('userStore')(UserCover)