import React,{useEffect,useState} from 'react';


import { ThemeProvider } from '@material-ui/core/styles';

import Typography from '@material-ui/core/Typography';
import Rating from '@material-ui/lab/Rating';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import ListSubheader from '@material-ui/core/ListSubheader';
import Divider from '@material-ui/core/Divider';
import VisibilityOffIcon from '@material-ui/icons/VisibilityOff';
import IconButton from '@material-ui/core/IconButton';
import GetAppIcon from '@material-ui/icons/GetApp';
import Paper from '@material-ui/core/Paper';
import axios from "axios";
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import { observer, inject} from 'mobx-react';

import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import { Link } from "react-router-dom";
import { Routes } from '../../../Routes'
import Grid from '@material-ui/core/Grid';
import Skeleton from '@material-ui/lab/Skeleton';
import Manga from '../../../Components/mangaCard/';
import Badge from '@material-ui/core/Badge';

const useStyles = makeStyles(theme => ({

}));





const UserMangaList = (props) => {
  const userId = props.match.params.userId
  const classes = useStyles();
  const [isLoading,setIsLoading]=useState(true)
  const [list,setList]=useState([])
  const fetchSubscriptions = async userId => {
    setIsLoading(true)
    console.log('fetchSub', userId)
    try {
      const response = await axios.get(`${process.env.REACT_APP_API_BASE}/user/subscriptions/${userId}`)
      console.log('üëâ Returned UserMangaList data:', response.data);
      setList(response.data)
      setIsLoading(false)
    } catch (e) {
      console.log(`üò± Axios request failed: ${e}`);
    }   
  }
  useEffect(()=>{
    fetchSubscriptions(userId)
  },[])


  return(
    <Grid style={{paddingTop: 16}} container spacing={2}>
    
    {
        !isLoading&&list.map(el=>
            <Grid key={el.id} item xs={6} sm={2}>
                <Link to={Routes.Manga.link(el.id)}>

                    <Manga src={el.imgUrl} load={false}
                    title={`${el.title} | ${el.englishTitle}`} 
                    subtitle={'Ongoing, ' + (el.lastChapter ? `–≥–ª–∞–≤–∞ ${el.lastChapter.volume} - ${el.lastChapter.chapter}` : '–≥–ª–∞–≤ –Ω–µ—Ç')}
                    />
                </Link>
            </Grid>
        )
    }
    {
    isLoading&&
        [...Array(10)].map((_,i)=>
            <Grid key={i} item xs={6} sm={2}>
                <Manga load />
            </Grid>
        )
    }
    </Grid>
  )}


  export default UserMangaList;